.presale-container {
  position: relative;
  padding-top: 3rem;

  img {
    position: absolute;
    width: 280px;
    transform: rotate(-8deg);
    top: -60px;

    @media (max-width: 991px) {
      width: 200px;
      top: -20px;
    }

    @media (max-width: 576px) {
      top: 0;
      left: -32px;
      width: 120px;
    }
  }

  .presale-button {
    display: block;
    margin: auto;
    padding-top: 2rem;
    padding-bottom: 2rem;
    width: 24rem;
    border: none;
    border-radius: 3rem;
    outline: none;
    background-color: #fbae59;
    color: #F1E5E7;
    font-family: "Wendy One";
    font-size: 4rem;
    font-style: normal;
    font-weight: 400;
    line-height: 3.2rem;
    z-index: 10;
    text-decoration: unset;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    &:hover {
      background-color: #f4764a;
      color: #fff;
    }

    @media (max-width: 768px) {
      font-size: 2.2rem;
      line-height: 2rem;
      width: 12rem;
    }

    @media (max-width: 576px) {
      font-size: 1.4rem;
      line-height: 1.2rem;
    }
  }
}

.section4 {
  background-size: contain;
  background-repeat: no-repeat;
  padding-top: 15%;
  padding-left: 23%;
  position: relative;
  aspect-ratio: 1691/1412;

  h2 {
    font-size: 8rem;
    font-family: "Wendy One";
    color: #ffbcd8;

    @media (max-width: 1200px) {
      font-size: 5rem;
    }
    @media (max-width: 991px) {
      font-size: 4rem;
    }
    @media (max-width: 768px) {
      font-size: 2.7rem;
    }
    @media (max-width: 576px) {
      font-size: 2.2rem;
    }
    @media (max-width: 450px) {
      font-size: 1.6rem;
    }
  }

  .content {
    margin-left: 15%;
    margin-right: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    font-family: "Wendy One";

    @media (max-width: 1100px) {
      gap: 1rem;
    }
    @media (max-width: 768px) {
      gap: 0rem;
    }

    .title-container {
      display: flex;
      align-items: center;
      gap: 1rem;
      margin-top: -20px;
      span {
        font-size: 8rem;
        @media (max-width: 1200px) {
          font-size: 5rem;
        }
        @media (max-width: 991px) {
          font-size: 4rem;
        }
        @media (max-width: 768px) {
          font-size: 3rem;
        }
        @media (max-width: 576px) {
          font-size: 2.2rem;
        }
        @media (max-width: 450px) {
          font-size: 1.6rem;
        }
      }
      img {
        max-width: 595px;
        width: 70%;
      }
    }
    ul {
      list-style: none;
      margin-left: -20%;

      @media (max-width: 350px) {
        margin-bottom: 0;
      }

      li {
        font-size: 2.5rem;
        text-align: center;
        line-height: 3.2rem;
        margin-bottom: 1.3rem;
        a {
          color: #ffbcd8;
        }
        p {
          margin-bottom: 0;
        }
        @media (max-width: 1600px) {
          font-size: 2rem;
          line-height: 2.5rem;
        }
        @media (max-width: 1440px) {
          font-size: 1.8rem;
          line-height: 2.2rem;
          width: 80%;
          margin: auto;
        }
        @media (max-width: 1300px) {
          font-size: 1.6rem;
          line-height: 2rem;
        }
        @media (max-width: 991px) {
          font-size: 1.2rem;
          line-height: 1.6rem;
          width: 100%;
          margin-bottom: 0.4rem;
        }
        @media (max-width: 768px) {
          font-size: 1rem;
          line-height: 1.4rem;
          width: 100%;
          margin-bottom: 0rem;
        }
        @media (max-width: 576px) {
          font-size: 0.8rem;
          line-height: 1rem;
          width: 100%;
        }
        @media (max-width: 400px) {
          font-size: 0.6rem;
          line-height: 0.8rem;
          width: 100%;
        }
      }
    }
    .logos {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: -15%;
      gap: 1.3rem;

      @media (max-width: 1200px) {
        gap: 0.4rem;
      }

      img {
        &.loh {
          width: 85px;

          @media (max-width: 1100px) {
            width: 50px;
          }
          @media (max-width: 650px) {
            width: 30px;
          }
          @media (max-width: 450px) {
            width: 20px;
          }
        }
        &.yurosako {
          width: 85px;
          @media (max-width: 1100px) {
            width: 50px;
          }
          @media (max-width: 650px) {
            width: 30px;
          }
          @media (max-width: 450px) {
            width: 20px;
          }
        }
        &.gangverse {
          width: 120px;
          @media (max-width: 1100px) {
            width: 80px;
          }
          @media (max-width: 650px) {
            width: 60px;
          }
          @media (max-width: 450px) {
            width: 40px;
          }
        }
      }
    }
  }

  .bg {
    // visibility: hidden;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
  }
}

.presale-text {
  font-family: Kalam;
  text-align: center;
  font-size: 2rem;
  margin-bottom: 10rem;

  @media (max-width: 768px) {
    font-size: 1.4rem;
    margin-bottom: 5rem;
  }
}

.ca-text {
  font-family: Kalam;
  text-align: center;
  font-size: 2rem;
  margin-top: 4rem;
  margin-bottom: 4rem;
  padding-left: 1rem;
  padding-right: 1rem;
  color: #ed1e79;
  word-wrap: break-word;

  @media (max-width: 768px) {
    font-size: 1.4rem;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }
}

.copy-button {
  display: block;
  margin: auto;
  border: none;
  border-radius: 3rem;
  outline: none;
  background-color: transparent;
  color: #0029ff;
  font-family: "Kalam";
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 3.2rem;
  z-index: 10;
  text-decoration: unset;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  @media (max-width: 768px) {
    font-size: 2.2rem;
    line-height: 2rem;
  }

  @media (max-width: 576px) {
    font-size: 1.4rem;
    line-height: 1.2rem;
  }
}